.Search {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.9);
}

.Search input {
  position: absolute;
  left: 20%;
  top: 60px;
  height: 100px;
  font-size: 60px;
  width: 60%;
  background-color: transparent;
  color: white;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
}

.Search .results {
  position: absolute;
  top: 220px;
  height: calc(100% - 220px);
}

.Search .results div {
  width: 100%;
  font-size: 40px;
  color: white;
}


