.Picture {
  position: relative;
  display: inline-block;
  margin: 20px;
  width: 25vw;
  height: 25vw;
  background-size: cover;
}

.Picture .instagram-layer {
  position: absolute;
  background-color: rgba(255,255,255,.5);
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.Picture .instagram-layer .social-icon {
  position: absolute !important;
  top: calc(50% - 25px);
  left: calc(50% - 25px);
}

.Picture:hover .instagram-layer {
  opacity: 1;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}


